<template>
  <el-container class="myhospital">
    <el-header style="height: 35px;">
      <div class="grid-content fonttop">
        <span>就诊人：</span>
        <span>{{from.name}}</span>
        <span style="margin:10px">{{from.nbr}}</span>
        <span style="float: right;line-height: 15px;">
          <el-button class="cut">切换</el-button>
        </span>
      </div>
    </el-header>
    <el-main>
      <el-card class="box-card" style="padding: 5px 0;">
        <el-row>
          <el-col :span="8">
            <div class="grid-content bg-purple"></div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple-light">
              <p style="color:#FF7800;font-size:22px;">
                ￥
                <span>5000</span>
              </p>
              <p class="myhospital_time_ft">预交金余额</p>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple myhospital_time">
              住院中，第
              <span>3</span>天
            </div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="grid-content bg-purple">
              <p>
                <span class="font_bold">200000.00</span>
              </p>
              <p class="myhospital_time_ft">预交金余额(元)</p>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="grid-content bg-purple">
              <p>
                <span class="font_bold">10000.00</span>
              </p>
              <p class="myhospital_time_ft">总费用(元)</p>
            </div>
          </el-col>
        </el-row>
        <el-divider class="myhospital_hr"></el-divider>
        <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark">
              <div class="myhospital_border">
                <p>
                  担保金额：
                  <span>25000.00</span>
                </p>
              </div>
            </div>
          </el-col>
        </el-row>
        <el-row class="myhospital_b">
          <el-col :span="12">
            <div class="grid-content bg-purple-light">
              <router-link tag="span" to="/shopcart/Prepay/dailylistingIn">
                <el-button type="primary" class="myhospital_bt" plain>住院日清单</el-button>
              </router-link>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="grid-content bg-purple-light">
              <router-link tag="span" to="/shopcart/Prepay/prepayment">
                <el-button type="primary" class="myhospital_bt">预交金充值</el-button>
              </router-link>
            </div>
          </el-col>
        </el-row>
      </el-card>
      <!--  -->
      <el-card class="box-card myhospital_LIST">
        <div>
          <el-row>
            <el-col :span="8" class="myhospital_list_left">
              <div class="grid-content bg-purple">姓名</div>
              <div class="grid-content bg-purple">性别</div>
              <div class="grid-content bg-purple">年龄</div>
              <div class="grid-content bg-purple">科室</div>
              <div class="grid-content bg-purple">入院时间</div>
            </el-col>
            <el-col :span="16" class="myhospital_list_right">
              <div class="grid-content bg-purple-light">
                <span>{{from.name}}</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>女性</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>21</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>vip病区</span>
              </div>
              <div class="grid-content bg-purple-light">
                <span>data</span>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
    </el-main>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<script>
import { mapGetters,mapState } from "vuex";
export default {
  data() {
    return {
      // nbr: this.$route.params.nbr,
      // name: this.$route.params.name
    };
  },
  created() {
  },
  computed: {
    ...mapState(["from"])
  }
};
</script>



